<template>
    <div class="main">
        <div>
            <!-- 二级路由 -->
            <router-view></router-view>
        </div>
        <van-tabbar v-model="activeIndex" active-color="#0C34BA" inactive-color="#646566" route>
            <van-tabbar-item v-for="(item,index) in tabbarData" :key="index" :badge="item.badge>0?item.badge:''" :dot="item.dot" :to="item.to">
                <span>{{item.title}}</span>
                <template #icon="props">
                    <img :src="props.active?item.icon.active:item.icon.inactive" />
                </template>
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
    import '../assets/less/main.less'
    import {mapState,mapMutations} from 'vuex'
    export default {
        name:'Main',
        data(){
            return {
                activeIndex:0,
            }
        },
        computed:{
            ...mapState(["shopbagCount","isLoadShopbagCount","isNewOrderMsg","isNewLike"]),
            tabbarData(){
                return [
                    {
                        title:'首页',
                        icon:{
                            active:require('../assets/images/home_active.png'),
                            inactive:require('../assets/images/home.png')
                        },
                        to:"/"
                    },
                    {
                        title:'菜单',
                        icon:{
                            active:require('../assets/images/menu_active.png'),
                            inactive:require('../assets/images/menu.png')
                        },
                        to:"/menu"
                    },
                    {
                        title:'购物车',
                        icon:{
                            active:require('../assets/images/shopbag_active.png'),
                            inactive:require('../assets/images/shopbag.png')
                        },
                        badge:this.shopbagCount,
                        to:"/shopcar"
                    },
                    {
                        title:'我的',
                        icon:{
                            active:require('../assets/images/my_active.png'),
                            inactive:require('../assets/images/my.png')
                        },
                        dot:this.isNewOrderMsg||this.isNewLike,
                        to:"/my"
                    }
                ]
            }
        },
        created(){
            this.getShopbagCount();
        },
        methods:{
            //获取商品总数量
            getShopbagCount(){
                if(this.isLoadShopbagCount){
                    return ;
                }
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，直接退出
                if(!tokenString){
                    return ;
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/shopcartRows',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，//直接退出
                    if(res.data.code == 700) {
                        return ;
                    }
                    //tokenString正确
                    if(res.data.code == 8000){
                        //提交changeShopbagCount的mutations
                        this.changeShopbagCount(res.data.result);
                        this.changeIsLoadShopbagCount(true);
                    }
                }).catch((err) => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            ...mapMutations(["changeShopbagCount","changeIsLoadShopbagCount"])
        }
    }
</script>

<style lang="less" scoped>
    .main {
        padding-bottom: 50px;
        background-color: #F8F8F8;
    }
</style>